import BattlePokemonRepository from "./PokemonCustomRepository";
//    
// , pp, , ,      .
//   origin 
class PokemonOnBattle {
  constructor(id) {
    this.team = ""; // player or npc
    this.id = id;
    const pokemon = BattlePokemonRepository.getItemById(id);
    this.origin = pokemon;
    this.name = pokemon.name; //    
    this.names = pokemon.names;
    this.hp = pokemon.hp; //  
    this.type1 = pokemon.type1;
    this.type2 = pokemon.type2;
    this.item = pokemon.item;
    this.itemText = pokemon.itemText;
    this.abil = pokemon.abil;
    this.abilObj = pokemon.abilObj;
    this.pp1 = pokemon.sk1.pp;
    this.pp2 = pokemon.sk2.pp;
    this.pp3 = pokemon.sk3.pp;
    this.pp4 = pokemon.sk4.pp;
    this.status = {
      //  damage.js 
      burn: null,
      freeze: null,
      poison: null, //
      mpoison: null, //
      mabi: null,
      sleep: null,
    };
    this.temp = {
      //   
      // turnEnd.js 
      fullDeath: null, //
      jumpKickFail: null, //    -> 1. 2. 3.()
      roost: null, //
      critical: null, //         
      recentDamageGive: null, //    -> ,   
      recentDamageGet: null,
      protect: null, //
      useSkill: null, //  () 
    };
    this.tempStatus = {
      // 
      //switchPokemon.js 
      rank: {
        // 
        atk: 0,
        def: 0,
        catk: 0,
        cdef: 0,
        speed: 0,
        critical: 0,
      },
      confuse: null,
      onlySkill: null, //    
      recentSkillUse: null, //    ->  ()  .
      recentSkillGet: null,
      hapum: null, //
      protectUse: null, //    ->    
      taunt: null, //
      protosynthesis: null, //
      protosynthesisBySun: null, //   ->      
      seed: null, //
      substitute: null, // 
      substituteHp: null, //    
      flashFire: null, // 
      switchLock: null, //  (   )
      switchLockTurnRemain: null, //   
    };
    this.faint = null; // 
    this.auto = null; //   ()
    this.autoSN = null;
    this.charge = null; //  (    )
    this.log = {
      damage1: null,
      damage2: null,
      speedCalculate: null,
      speedVS: null,
    };
  }
}

export function generate(id) {
  return new PokemonOnBattle(id);
}
