import React from "react";
import styled from "styled-components";
import { typeCheckConsole } from "../../../util/typeCheck";
import { battleStart } from "../../../service/battleStart";
import { npcChoice } from "../../../npc/npc";

const SkillButton = ({ battle, skillNumber, queueObject, setText }) => {
  const skill = "sk" + skillNumber;
  const pp = "pp" + skillNumber;
  let sk = battle.player.origin[skill];

  const sn = getNumberText(skillNumber);

  const handleDequeue = () => {
    if (queueObject.queue.length > 0) {
      if (battle.turn.textFreeze) {
        return;
      }
      queueObject.dequeue();
    }
  };

  const handleSkillClick = (skillIndex) => {
    handleDequeue();
    //dequeue     
    //dequeue      dequeue  
    //stopPropagation  dequeue   

    const player = battle.player;
    const skill = "sk" + skillIndex;
    const sk = player.origin[skill]; //   
    const pp = player["pp" + skillIndex];

    if (!queueObject.queueCheck()) return;

    const reject = (message) => {
      if (!player.auto && !player.charge) {
        //enqueue     
        queueObject.enqueue({ battle, text: message, skip: true });
        setText?.(message);
      }
      return true;
    };

    //  PP 
    if (pp <= 0) return reject("      !");

    //        
    const { onlySkill } = player.tempStatus;
    if (onlySkill && player.item?.startsWith("") && onlySkill !== sk.name) {
      return reject(player.item + "       !");
    }

    //     (ex: )
    const recentSkill = player.tempStatus.recentSkillUse?.name;
    if (recentSkill === sk.name && sk.name === "") {
      return reject("     !");
    }

    //    
    const tauntActive = player.tempStatus.taunt !== null;
    if (tauntActive && (sk.stype === "natk" || sk.stype === "buf")) {
      return reject("      !");
    }

    //    
    const dolJo = player.item === "";
    if (dolJo && (sk.stype === "natk" || sk.stype === "buf")) {
      return reject("      !");
    }

    //      
    battleStart(battle, skillIndex, npcChoice(battle, skillIndex), queueObject);
  };

  return (
    <SKILL
      className={sn}
      onClick={(e) => {
        e.stopPropagation(); //   onClick(handleDequeue)   
        handleSkillClick(skillNumber);
      }}>
      <ICON src={`/pokemon/img/type/${sk.type}.svg`} alt={sk.name} />
      <NAME skname={sk.name}>{sk.name}</NAME>
      <EFFECT>{typeCheckConsole(battle.player, sk.type, battle.npc.type1, battle.npc.type2, sk.stype)}</EFFECT>
      <PP>
        {battle.player[pp]}/{sk.pp}
      </PP>
    </SKILL>
  );
};

const getNumberText = (value) => {
  switch (value) {
    case 1:
      return "one";
    case 2:
      return "two";
    case 3:
      return "three";
    case 4:
      return "four";
    default:
      return "Invalid value"; //  1~4  
  }
};

export default SkillButton;

const SKILL = styled.div`
  position: absolute;
  width: 47vw;
  height: 13vh;
  border-radius: 5px;
  background-color: rgba(0, 0, 0, 0.7);

  &.one {
    top: 20vh;
    left: 1vw;
  }
  &.two {
    top: 20vh;
    right: 1vw;
  }
  &.three {
    top: 35vh;
    left: 1vw;
  }
  &.four {
    top: 35vh;
    right: 1vw;
  }
`;

const ICON = styled.img`
  position: absolute;
  width: 30px;
  height: 30px;
  top: 5px;
  left: 5px;
  border-radius: 5px;
`;

const NAME = styled.div`
  position: absolute;
  top: 10px;
  left: 42px;
  font-size: "20px";
  @media (max-width: 400px) {
    font-size: ${({ skname }) => (skname.length > 5 ? "4.3vw" : "20px")};
    top: ${({ skname }) => (skname.length > 5 ? "12px" : "10px")};
  }
`;

const EFFECT = styled.div`
  position: absolute;
  top: 45px;
  left: 7px;
  font-size: 15px;
`;

const PP = styled.div`
  position: absolute;
  bottom: 4px;
  right: 3px;
  font-size: 15px;
  background-color: #665f5f;
  border-radius: 5px;
`;
