import { typeCheckAbil } from "./typeCheck";
import { getMultiplier } from "../function/rankStat";
import { noNullItem } from "../entity/Item";
import { flyingCheck } from "./flyingCheck";
import { pokemonNoStatusCheck } from "../function/statusCondition";

export const damageCalculate = (battle, serial) => {
  //  ""   
  // battle      (log )
  // =>  ,   ,  damage  
  //   
  const attackPokemon = battle[battle.turn.atk];
  const defensePokemon = battle[battle.turn.def];

  const skillNumber = battle.turn.atkSN;
  const skillKey = `sk${skillNumber}`;
  const sk = attackPokemon.origin[skillKey]; //  

  const atkAbil = attackPokemon.abil;
  const defAbil = defensePokemon.abil;

  const noTggAtk = !attackPokemon.abilObj.feature?.tgg;

  //    ===============================================================================================

  if (sk.feature.oneShot) {
    //
    return defensePokemon.origin.hp;
  }
  if (sk.name === "") {
    return defensePokemon.hp / 2;
  }
  if (sk.name === "") {
    return 50;
  }
  if (sk.feature.reflect) {
    //    skillRequurement 
    return Math.floor(attackPokemon.temp.recentDamageGet) * 2;
  }

  //  ================================================================================================================

  //    .
  //  ,     
  //   ,    ,      

  let attackStat = sk.stype;
  let defenseStat = "";
  if (attackStat === "atk") {
    //     
    defenseStat = "def";
  } else if (attackStat === "catk") {
    //     
    defenseStat = "cdef";
    if (sk.name === "") {
      defenseStat = "def";
    }
  }

  if (sk.name === "") {
    //     
    attackStat = "def";
  }

  //  ================================================================================================================
  //      (, , ) 
  // :   .
  // :    
  // :   

  let noRankAtkStat = attackPokemon.origin.stat[attackStat];
  let rankNumAtk = attackPokemon.tempStatus.rank[attackStat];
  let rankUpAtk = getMultiplier(rankNumAtk);
  let atkStat = noRankAtkStat * rankUpAtk;
  if (defAbil === "") {
    if (noTggAtk) {
      atkStat = noRankAtkStat;
    }
    //  
    //    
    //      (ex:  )
  }
  if (attackPokemon.temp.critical && rankNumAtk < 0) {
    atkStat = noRankAtkStat;
    //       
    //  
  }

  //  / 
  let noRankDefStat = defensePokemon.origin.stat[defenseStat];
  let rankNumDef = defensePokemon.tempStatus.rank[defenseStat];
  let rankUpDef = Math.floor(getMultiplier(rankNumDef));
  let defStat = noRankDefStat * rankUpDef;
  //  
  //    
  for (const effect of sk.skillEffectList) {
    if (effect.name === "") {
      defStat = noRankDefStat;
    }
  }

  //  
  //    
  //     
  if (attackPokemon.abil === "") {
    defStat = noRankDefStat;
  }
  //       
  if (attackPokemon.temp.critical && defensePokemon.tempStatus.rank[defenseStat] > 0) {
    defStat = noRankDefStat;
  }

  atkStat = Math.floor(atkStat);
  defStat = Math.floor(defStat);
  //  ================================================================================================================

  if (sk.name === "") {
    atkStat = defensePokemon.origin.stat.atk * getMultiplier(defensePokemon.tempStatus.rank.atk);
    if (defensePokemon.abil === "") {
      atkStat = defensePokemon.origin.stat.atk;
    }
    //     .
    //   ,  , ,   
    //  , ,  
    //      (   )
    //        
  }

  //   ======================================================================================================
  let atkStr = atkStat + "(" + attackStat + ")";
  let defStr = defStat + "(" + defenseStat + ")";

  //  (atk) ===================================
  if (attackStat === "atk") {
    if (attackPokemon.item === "") {
      atkStat *= 1.5;
      atkStr += " * 1.5 ()";
    }

    const isStatus = Object.values(attackPokemon.status).some((v) => v !== null);
    if (atkAbil === "" && isStatus) {
      atkStat *= 1.5;
      atkStr += " * 1.5 ()";
    }

    if (attackPokemon.tempStatus.protosynthesis === "atk") {
      atkStat *= 1.3;
      atkStr += " * 1.3 ()";
    }

    if (attackPokemon.status.burn !== null && attackStat === "atk" && attackPokemon.abil !== "") {
      //    
      atkStat *= 0.5;
      atkStr += " * 0.5 ()";
    }

    if (battle.field.noClean[battle.turn.def].reflect) {
      if (sk.feature?.wallBreaker) {
      } else {
        atkStat *= 0.5;
        atkStr += " * 0.5 ()";
      }
    }

    if (defAbil === "" && atkAbil !== "") {
      if (noTggAtk) {
        // ,   ,   .
        // (      ''   )
        atkStat *= 0.75;
        atkStr += " * 0.75 ()";
      }
    }
    if (attackPokemon.abil === "" && battle.field.weather === "") {
      atkStat *= 1.3;
      atkStr += " * 1.3 ()";
    }
  }

  //  (catk) ===================================
  if (attackStat === "catk") {
    if (attackPokemon.item === "") {
      atkStat *= 1.5;
      atkStr += " * 1.5 ()";
    }
    if (defAbil === "" && atkAbil !== "") {
      if (noTggAtk) {
        atkStat *= 0.75;
        atkStr += " * 0.75 ()";
      }
    }

    if (battle.field.noClean[battle.turn.def].lightScreen) {
      //    
      if (sk.feature?.wallBreaker) {
      } else {
        atkStat *= 0.5;
        atkStr += " * 0.5 ()";
      }
    }
    if (attackPokemon.tempStatus.protosynthesis === "catk") {
      atkStat *= 1.3;
      atkStr += " * 1.3 ()";
    }
    if (attackPokemon.abil === "" && battle.field.field === "") {
      atkStat *= 1.3;
      atkStr += " * 1.3 ()";
    }
  }

  //  (cdef) ===================================
  if (defenseStat === "def") {
    if (defensePokemon.tempStatus.protosynthesis === "def") {
      defStat *= 1.3;
      defStr += " * 1.3 ()";
    }
    if (atkAbil === "" && defAbil !== "") {
      defStat *= 0.75;
      defStr += " * 0.75 ()";
    }
    if (defensePokemon.item === "" && defensePokemon.origin.feature?.evo) {
      defStat *= 1.5;
      defStr += " * 1.5 ()";
    }
  }

  //  (cdef) ===================================
  if (defenseStat === "cdef") {
    if (defensePokemon.item === "") {
      defStat *= 1.5;
      defStr += " * 1.5 ()";
    }
    if (defensePokemon.tempStatus.protosynthesis === "cdef") {
      defStat *= 1.3;
      defStr += " * 1.3 ()";
    }
    if (atkAbil === "" && defAbil !== "") {
      defStat *= 0.75;
      defStr += " * 0.75 ()";
    }
    if (defensePokemon.item === "" && defensePokemon.origin.feature?.evo) {
      defStat *= 1.5;
      defStr += " * 1.5 ()";
    }
  }

  //  ======================================================================================================

  let power = powerCalculate(battle, sk, serial);
  //     
  // ex) ,  

  //  ======================================================================================================

  let damage = (22 * power * atkStat) / 50 / defStat; // 
  attackPokemon.log.damage1 = "22 * " + power + "() * [" + atkStr + "] / 50 / [" + defStr + "]";

  //  ==========================================================
  const weather = battle.field.weather; //  
  if (weather === "") {
    if (sk.type === "") {
      damage *= 1.5;
      attackPokemon.log.damage1 += " * 1.5 (+)";
    }
    if (sk.type === "") {
      damage *= 0.5;
      attackPokemon.log.damage1 += " * 0.5 (+)";
    }
  }
  if (weather === "") {
    if (sk.type === "") {
      damage *= 1.5;
      attackPokemon.log.damage1 += " * 1.5 (+)";
    }
    if (sk.type === "") {
      damage *= 0.5;
      attackPokemon.log.damage1 += " * 0.5 (+)";
    }
  }

  //  ==========================================================

  const field = battle.field.field;
  if (field !== null) {
    if (!flyingCheck(battle, attackPokemon)) {
      if (field === "" && sk.type === "") {
        damage *= 1.3;
        attackPokemon.log.damage1 += " * 1.3 (+)";
      }
      if (field === "" && sk.type === "") {
        damage *= 1.3;
        attackPokemon.log.damage1 += " * 1.3 (+)";
      }
    }

    if (field === "" && sk.name === "") {
      damage *= 0.5; //  
      attackPokemon.log.damage1 += " * 0.5 (+)";
    }
  }

  //  ==========================================================

  if (sk.feature?.punch && attackPokemon.item === "") {
    damage *= 1.1;
    attackPokemon.log.damage1 += " * 1.1 ()";
  }

  //  ===========================================================================================

  if (defAbil === "" && defensePokemon.hp === defensePokemon.origin.hp) {
    if (noTggAtk) {
      damage *= 0.5;
      attackPokemon.log.damage1 += " * 0.5 ()";
    }
    //    
    //      ()
    //    
    //   (, , , )   
  }

  if ((atkAbil === "" || defAbil === "") && sk.type === "") {
    //   
    damage = (damage * 4) / 3;
    attackPokemon.log.damage1 += " * 4/3 ()";
  }

  if ((atkAbil === "" || defAbil === "") && sk.type === "") {
    damage = (damage * 4) / 3;
    attackPokemon.log.damage1 += " * 4/3 ()";
  }

  if (atkAbil === "" && sk.feature?.rebound) {
    damage *= 1.2;
    attackPokemon.log.damage1 += " * 1.2 ()";
  }

  if (atkAbil === "" && sk.feature?.bite) {
    damage *= 1.5;
    attackPokemon.log.damage1 += " * 1.5 ()";
  }

  if (sk.type === "" && attackPokemon.tempStatus.flashFire) {
    damage *= 1.5;
    attackPokemon.log.damage1 += " * 1.5 ()";
  }

  // ======================================================================  [ ] ========================================
  // ===========================================================================================================================
  // ===========================================================================================================================

  damage = Math.floor(damage);
  attackPokemon.log.damage1 += " = " + damage;
  damage += 2; //   (   )
  attackPokemon.log.damage2 = damage;

  //  ===============================================================
  if (sk.type === attackPokemon.type1 || sk.type === attackPokemon.type2) {
    if (atkAbil === "") {
      damage *= 2;
      attackPokemon.log.damage2 += " * 2 (+)";
    } else {
      damage *= 1.5;
      attackPokemon.log.damage2 += " * 1.5 ()";
    }
  }

  //  ================================================================
  const typeDamege = typeCheckAbil(battle, sk.type, defensePokemon.type1, defensePokemon.type2); //  
  damage *= typeDamege;
  attackPokemon.log.damage2 += " * " + typeDamege + "()";

  if (sk.name === "") {
    //     
    if (typeDamege > 1) {
      damage = (damage * 4) / 3;
      attackPokemon.log.damage2 += " * 4/3 ( )";
    }
  }

  //   ================================================================

  if (attackPokemon.item === "") {
    damage *= 1.3;
    attackPokemon.log.damage2 += " * 1.3 ()";
  }

  //  =============================================================================
  if (attackPokemon.temp.critical) {
    damage *= 1.5;
    attackPokemon.log.damage2 += " * 1.5 ()";
  }

  //  ======================================================================================================

  const randomNum = getRandomNumber(); // 
  damage = (damage * randomNum) / 100;
  attackPokemon.log.damage2 += " * " + randomNum + "() / 100 = " + Math.floor(damage);
  console.log(attackPokemon.log.damage1);
  console.log(attackPokemon.log.damage2);
  return Math.floor(damage);
};

export const confuseDamageCalculate = (battle) => {
  //   
  //,  , ,   
  // ,   
  //  
  const pokemon = battle[battle.turn.atk];
  let atk = pokemon.atk;
  let def = pokemon.def;
  if (pokemon.temp.critical && pokemon.tempStatus.rank[def] > 0) {
    def = pokemon.origin[def];
    //       
  }
  if (pokemon.temp.critical && pokemon.tempStatus.rank[atk] < 0) {
    atk = pokemon.origin[atk];
    //       
    //  
  }
  let damage = (22 * 40 * atk) / 50 / def;
  damage += 2;
  if (pokemon.temp.critical) {
    damage *= 1.5;
  }
  const randomNum = getRandomNumber();
  damage = (damage * randomNum) / 100;
  return Math.floor(damage);
};

const getRandomNumber = () => {
  //   
  const numbers = [
    { num: 85, prob: 7.69 },
    { num: 86, prob: 5.13 },
    { num: 87, prob: 7.69 },
    { num: 88, prob: 5.13 },
    { num: 89, prob: 7.69 },
    { num: 90, prob: 7.69 },
    { num: 91, prob: 5.13 },
    { num: 92, prob: 7.69 },
    { num: 93, prob: 5.13 },
    { num: 94, prob: 7.69 },
    { num: 95, prob: 5.13 },
    { num: 96, prob: 7.69 },
    { num: 97, prob: 5.13 },
    { num: 98, prob: 7.69 },
    { num: 99, prob: 5.13 },
    { num: 100, prob: 2.56 },
  ];

  //    
  let cumulative = 0;
  const weightedNumbers = numbers.map(({ num, prob }) => {
    cumulative += prob;
    return { num, cumulative };
  });

  // 0~100   
  const randomValue = Math.random() * 100;

  //     
  return weightedNumbers.find(({ cumulative }) => randomValue <= cumulative).num;
};

const powerCalculate = (battle, skill, serial) => {
  const atk = battle[battle.turn.atk];
  let power = skill.power;
  const itemName = battle[battle.turn.def].item;
  if (skill.name === "") {
    //         1.5
    if (itemName !== null && !noNullItem.includes(itemName)) power *= 1.5;
  }
  if (skill.name === "") {
    //    2
    if (battle.turn.fastActUser === battle.turn.atk || !battle[battle.turn.fastActUser].temp.useSkill) power *= 2;
    //         2 
  }
  if (skill.name === "") {
    //  
    power = (power * atk.hp) / atk.origin.hp;
  }
  if (skill.name === "" && (atk.status.burn || atk.status.mabi || atk.status.poison || atk.status.mpoison)) {
    //, ,   2
    power *= 2;
  }
  if (skill.name === "") {
    //    2
    if (!pokemonNoStatusCheck(battle[battle.turn.def])) power *= 2;
  }
  if (skill.name === "") {
    //    2
    if (battle[battle.turn.atk + "Bench1"].faint) power += 50;
    if (battle[battle.turn.atk + "Bench2"].faint) power += 50;
  }
  if (skill.name === "") {
    if (serial === 1) power = 20;
    if (serial === 2) power = 40;
    if (serial === 3) power = 60;
  }
  if (skill.name === "") {
    //
    const total = Object.values(atk.tempStatus.rank).reduce((sum, v) => {
      return sum + (typeof v === "number" && v > 0 ? v : 0);
    }, 0);
    power += 20 * total;
  }
  return Math.floor(power);
};

const statusCheck = (status) => {
  return Object.values(status).some((value) => value !== null);
};
