// random    export
export function flyingCheck(battle, poke) {
  //  
  //      

  let pokemon;
  if (poke === "npc") {
    pokemon = battle.npc;
  } else if (poke === "player") {
    pokemon = battle.player;
  } else if (typeof poke === "object" && poke !== null) {
    pokemon = poke; //      
  }
  let enemy = pokemon.team === "player" ? battle["npc"] : battle["player"];

  if (pokemon.abil === "" && enemy?.abilObj.feature?.tgg !== true) {
    //     !
    return true;
  }
  if (pokemon.item === "") {
    return true;
  }
  if (pokemon.type1 === "" || pokemon.type2 === "") {
    if (pokemon.temp.roost) {
      //
      return false;
    }
    return true;
  }

  return false;
}
