export const typeCheck = (stype, type1, type2) => {
  //      
  // field.js    
  if (typeChart[stype][type1] === 0 || typeChart[stype][type2] === 0) {
    return 0;
  }
  let typeDamage = 1;
  if (typeChart[stype]) {
    if (typeChart[stype][type1]) {
      typeDamage *= typeChart[stype][type1];
    }
    if (typeChart[stype][type2]) {
      typeDamage *= typeChart[stype][type2];
    }
  }

  return typeDamage;
};

export const typeCheckConsole = (pokemon, stype, type1, type2, skillType) => {
  //     
  //      
  // , ,      
  //      
  // skillButton(), InfoSkillButton()  

  if (skillType === "natk" || skillType === "buf") {
    return;
  }

  if (typeChart[stype][type1] === 0 || typeChart[stype][type2] === 0) {
    if (pokemon.abil === "" && (stype === "" || stype === "") && (type1 === "" || type2 === "")) {
      //     
    } else {
      return "  ";
    }
  }
  let typeDamage = 1;
  if (typeChart[stype]) {
    if (typeChart[stype][type1]) {
      //0  (   )
      typeDamage *= typeChart[stype][type1];
    }
    if (typeChart[stype][type2]) {
      typeDamage *= typeChart[stype][type2];
    }
  }

  if (typeDamage === 1) {
    return "  ";
  }
  if (typeDamage > 1) {
    return "  ";
  }
  if (typeDamage === 0) {
    return "  ";
  }
  if (typeDamage < 1) {
    return "  ";
  }
};

export const typeCheckAbil = (battle, stype, type1, type2) => {
  //    
  //        

  // damageCalculate 
  // useSkill   (   ...)    ()  
  if (typeChart[stype][type1] === 0 || typeChart[stype][type2] === 0) {
    if (battle[battle.turn.atk].abil === "" && (stype === "" || stype === "") && (type1 === "" || type2 === "")) {
      //  
    } else {
      return 0;
    }
  }
  let typeDamage = 1;
  if (typeChart[stype]) {
    if (typeChart[stype][type1]) {
      //0  (   )
      typeDamage *= typeChart[stype][type1];
    }
    if (typeChart[stype][type2]) {
      typeDamage *= typeChart[stype][type2];
    }
  }

  return typeDamage;
};

export const typeCheckText = (typeDamage) => {
  //   
  // skillUse typeCheckAbil      
  if (typeDamage === 1) {
    return null;
  }
  if (typeDamage > 1) {
    return " !";
  }
  if (typeDamage === 0) {
    return "   ...";
  }
  if (typeDamage < 1) {
    return "   ...";
  }
};

